import React from 'react';
import { Col, Row } from 'antd';
import styled from 'styled-components';
import {
  ContainerWrapper,
  SecondaryDescription,
  Section,
  SectionHeader
} from '@src/Styles';
import { device, size } from '@src/breakpoints';
import ethereum_blockchain from '@images/about/ethereum_blockchain.svg';

export const TextWrapper = styled.div`
  padding: 0px 10px;

  h2,
  p {
    text-align: left;
  }

  @media ${device.mobileS} and (max-width: ${size.tablet}) {
    padding: 30px 0px 0px 0px;
  }

  @media ${device.tablet} and (max-width: ${size.laptopL}) {
    padding: 0px 30px;
  }

  @media ${device.laptopL} {
    padding: 0px 40px;
  }
`;

export const IllustrationWrapper = styled.div`
  background: #00E2C1
  justify-content: center;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 70px

  @media ${device.mobileS} and (max-width: ${size.tablet}) {
    padding: 50px;
  }
`;

export const ModifiedContainerWrapper = ContainerWrapper.extend`
  @media ${device.tablet} and (max-width: ${size.laptop}) {
    max-width: 880px;
  }
`;

class Trading extends React.Component {
  render() {
    return (
      <Section bg={'#000000'}>
        <ModifiedContainerWrapper>
          <Row type="flex" align="middle">
            <Col xs={24} sm={24} md={12} lg={12} xl={12}>
              <IllustrationWrapper>
                <img
                  width="100%"
                  alt="Ethereum Blockchain"
                  src={ethereum_blockchain}
                  style={{ margin: '0 auto' }}
                />
              </IllustrationWrapper>
            </Col>
            <Col xs={24} sm={24} md={12} lg={12} xl={12}>
              <TextWrapper>
                <SectionHeader color={'#fff'}>
                  Derivatives trading on the Ethereum blockchain
                </SectionHeader>
                <SecondaryDescription
                  style={{
                    color: '#ffffff'
                  }}
                >
                  MARKET Protocol has created the open-source foundation needed
                  to create Position Tokens that track the price of any asset.
                  Following the ERC-20 standard, these tokens are tradeable on
                  any exchange or wallet, delivering value to anyone in the
                  world.
                </SecondaryDescription>
                <SecondaryDescription
                  style={{
                    color: '#ffffff'
                  }}
                >
                  MARKET Protocol is a framework that enables traders and
                  businesses to buy and sell digital and real-world assets in a
                  safe, solvent and trustless environment.
                </SecondaryDescription>
              </TextWrapper>
            </Col>
          </Row>
        </ModifiedContainerWrapper>
      </Section>
    );
  }
}

export default Trading;



