import React from 'react';
import { Col, Row } from 'antd';
import styled from 'styled-components';
import shortSelling from '@images/about/short_selling.svg';
import hedging from '@images/about/hedging.svg';
import custody from '@images/about/custody.svg';
import {
  ContainerWrapper,
  MarketHeader,
  SecondaryDescription,
  SecondaryHeader,
  Section,
  SectionHeader
} from '@src/Styles';
import { device, size } from '@src/breakpoints';

export const HeaderText = MarketHeader.extend`
  padding-bottom: 40px;
  text-align: center;

  @media ${device.mobileS} and (max-width: ${size.mobileL}) {
    padding: 60px 20px 20px 20px;
  }
`;

export const Wrapper = styled.div`
  background-color: ${props => props.bg || '#fff'};
  justify-content: center;
  padding: 50px;
  display: flex;
  flex-direction: column;
  text-align: center;

  @media ${device.mobileL} {
    height: 350px;
  }
`;
export const ImageContainer = styled.img`
  margin-bottom: 10px;
`;

class Derivatives extends React.Component {
  render() {
    return (
      <Section bg={'#f0f0f0'}>
        <ContainerWrapper>
          <SectionHeader color={'#000'}>How Derivatives can help</SectionHeader>
          <Row type="flex">
            <Col xs={24} md={12} lg={8}>
              <Wrapper bg="#fff">
                <ImageContainer src={shortSelling} alt="Short Selling" />
                <SecondaryHeader>Short Selling</SecondaryHeader>
                <SecondaryDescription>
                  Can help reduce volatility
                </SecondaryDescription>
              </Wrapper>
            </Col>
            <Col xs={24} md={12} lg={8}>
              <Wrapper bg="#181E26">
                <ImageContainer src={hedging} alt="Hedging" />
                <SecondaryHeader
                  style={{
                    color: '#ffffff'
                  }}
                >
                  Hedging
                </SecondaryHeader>
                <SecondaryDescription
                  style={{
                    color: '#ffffff'
                  }}
                >
                  Essential for risk mitigation
                </SecondaryDescription>
              </Wrapper>
            </Col>
            <Col xs={24} md={24} lg={8}>
              <Wrapper bg="#00E2C1">
                <ImageContainer src={custody} alt="Custody Of Assets" />
                <SecondaryHeader>Custody of Assets</SecondaryHeader>
                <SecondaryDescription>
                  Price exposure is simpler
                </SecondaryDescription>
              </Wrapper>
            </Col>
          </Row>
        </ContainerWrapper>
      </Section>
    );
  }
}

export default Derivatives;



